:root {
  --green: #00ee6e;
  --red: #ef3e36;
  --black: #2e282a;
  --white: #fffcf9;
  --navbar-height: 64px;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #fffcf9;
  color: var(--white);
  background-color: #2e282a;
  background-color: var(--black);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.logo {
  background-image: url(/static/media/gecko-trivia.61697539.svg);
  background-repeat: no-repeat;
}

.nav-bar {
  width: 100vw;
  position: relative;
  top: 0;
  left: 0;
  margin-bottom: auto;
  padding: 5px 10px;
  display: flex;
  justify-content: right;
  align-items: center;
}

.nav-bar .logo {
  width: calc(64px * 136 / 100);
  width: calc(var(--navbar-height) * 136 / 100);
  height: 64px;
  height: var(--navbar-height);
  margin-right: auto;
}

.nav-bar ul {
  display: flex;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
}

.nav-bar li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a.link-btn,
.btn {
  color: #00ee6e;
  color: var(--green);
  text-decoration: none;
  display: inline-block;
  padding: 0.5em 1.75em;
  background-color: transparent;
  border-radius: 2em;
  border: 2px solid #00ee6e;
  border: 2px solid var(--green);
}

a.link-btn:hover,
.btn:hover {
  cursor: pointer;
  color: #ef3e36;
  color: var(--red);
  border-color: #ef3e36;
  border-color: var(--red);
}

.btn.green,
.link-btn.green {
  color: #2e282a;
  color: var(--black);
  background-color: #00ee6e;
  background-color: var(--green);
  border: none;
}

.btn.green:hover,
.link-btn.green:hover {
  color: #2e282a;
  color: var(--black);
  background-color: #00ee6e;
  background-color: var(--green);
  border: none;
}

.btn.red,
.link-btn.red {
  color: #fffcf9;
  color: var(--white);
  background-color: #ef3e36;
  background-color: var(--red);
  border: none;
}

.btn.red:hover,
.link-btn.red:hover {
  color: #fffcf9;
  color: var(--white);
  background-color: #ef3e36;
  background-color: var(--red);
  border: none;
}

.page {
  display: flex;
  flex-direction: column;
  margin-bottom: auto;
  margin-top: calc(-1 * 64px);
  margin-top: calc(-1 * var(--navbar-height));
  padding-top: 30px;
}

.homepage {
  align-items: center;
}

.homepage h1 {
  text-align: center;
}

.homepage .logo {
  width: 320px;
  height: 320px;
  background-size: contain;
}

.homepage-boxes {
  /* width: 80vw; */
  display: flex;
  grid-gap: 50px;
  gap: 50px;
  /* background-color: gray; */
}

.box .box-title {
  min-width: 20vw;
  font-size: 32px;
  text-align: center;
  /* background-color: gray; */
}

.box .box-body {
  margin-top: 50px;
  text-align: center;
  /* background-color: gray; */
}

.selection-btn {
  font-size: 24px;
  padding: 0.75em 2.5em;
}

.topic-select {
  font-size: 18px;
  outline: none;
  margin-bottom: 50px;
  padding: 0.75em 0.75em;
  border-radius: 3em;
  color: #fffcf9;
  color: var(--white);
  background-color: #ef3e36;
  background-color: var(--red);
}

.answer-button {
  font-size: 20px;
  padding: 0.75em 2.5em;
}

.quiz-page {
  align-items: center;
}

.quiz-template {
  width: min(800px, 80vw);
  height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 50px;
  text-align: center;
}

.question-container .question {
  font-weight: 400;
}

.answer-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 80px;
  gap: 80px;
}

.quiz-results {
  height: 80vh;
}

.main {
  text-align: center;
}

.quiz-results .header {
  text-align: center;
  margin-bottom: 40px;
}

.quiz-results h2 {
  font-size: 32px;
  color: #00ee6e;
  color: var(--green);
  margin-bottom: 40px;
}

.quiz-results p {
  margin-bottom: 40px;
}

.share-page p {
  margin-bottom: 40px;
}

.quiz-results .buttons {
  display: flex;
  grid-gap: 80px;
  gap: 80px;
}

.quiz-results .btn {
  font-size: 24px;
}

p > a:link {
  color: #ef3e36;
  color: var(--red);
  text-decoration: underline;
}

p > a:visited {
  color: #ef3e36;
  color: var(--red);
}

.card {
  width: 400px;
  height: 88vh;
  margin: 0 auto;
}

.card .header {
  text-align: center;
  margin-bottom: 40px;
}

.user-form {
  width: min(80vw, 400px);
  text-align: center;
}

.form-input {
  display: block;
  width: 100%;
  margin-bottom: 1em;
  padding: 1em 1.5em;
  font-size: 18px;
  border-radius: 2em;
  color: #fffcf9;
  color: var(--white);
  border: 2px solid #00ee6e;
  border: 2px solid var(--green);
  background: none;
}

.submit-btn {
  font-size: 22px;
  margin-top: 30px;
  padding: 0.75em 2.5em;
}

.form-input:focus {
  outline: none;
}

.status-msg {
  margin: 20px 0 10px;
  text-align: left;
}

.disabled-btn {
  background-color: #ccc;
  cursor: not-allowed;
}

.table {
  width: 100%;
  margin: 20px auto 0;
  padding: 10px 0;
  table-layout: fixed;
  border: 2px solid #00ee6e;
  border: 2px solid var(--green);
  border-radius: 20px;
}

.table th,
.table td {
  margin: 0;
  padding: 10px;
}

.table tfoot {
  color: #ef3e36;
  color: var(--red);
}
